﻿body, html {
	padding:	0;
	margin:		0;
	background-color:	rgba(38, 47, 61, 1);
	font-family:		'Open Sans', sans-serif;
}

code {
	font-size:			11px;
	background-color:	rgba(200, 230, 201, 1);
	padding:			2px 5px;
	border-radius:		2px;
}

.nobreak {
	overflow:		hidden;
	text-overflow:	ellipsis;
	white-space:	nowrap;
}

.ace_editor { height: 200px; }

.link {
	font-size:		10px;
	font-weight:	500;
	color:			rgba(66, 165, 245, 0.7);
	transition:		color ease-out 0.3s;
	&:hover {
		text-decoration:	underline;
	}
}

.loading-indicator {
	padding:	20px auto;
	font-size:	25px;
	color:		#000000;
	text-align:	center;
}


.ace_editor {
	height:	300px;
	
	&.xs {
		height:	50px;
	}
	&.small {
		height:	150px;
	}
	&.medium {
		height:	500px;
	}
	&.large {
		height:	700px;
	}
}



.ui-tools {
	position:	absolute;
	top:		0;
	left:		0;
	width:		100%;
	height:		100%;
	overflow-y:	auto;
	font-family: 'Roboto', sans-serif;
	
	background: rgb(225,228,234);
	background: linear-gradient(180deg, rgba(225,228,234,1) 0%, rgba(227,234,240,1) 100%);
	
	& > .ui-header {
		background-color:	#ffffff;
		color:				#333333;
		font-weight:		600;
		font-size:			14px;
		text-align:			center;
		line-height:		40px;
		border-bottom:		1px solid rgba(0,0,0,0.4);
		& > div {
			display:	table;
			width:		100%;
			& > div {
				display:	table-row;
				& > div {
					display:		table-cell;
					text-align:		center;
					vertical-align:	middle;
					padding:	5px;
					&.logo {
						text-align:		left;
						font-size:		16px;
						font-weight:	900;
						letter-spacing:	2px;
						padding-left:	20px;
					}
					&.menu {
						text-align:	right;
						.menu {
							& > div, & > a {
								display:		inline-block;
								vertical-align:	top;
								padding:		2px 10px;
								font-size:		12px;
								font-weight:	600;
								color:			#333333;
								text-decoration:	none;
								&:hover {
									color:		rgba(66, 165, 245, 1);
									text-decoration:	none;
								}
							}
						}
					}
					&.sep {
						width:		5px;
						text-align:	center;
						
						& > div {
							height:	20px;
							width:	1px;
							background-color:	rgba(0,0,0, 0.6);
							display:			inline-block;
							vertical-align:		middle;
						}
					}
					& > div {
						display:	inline-block;
						margin:		0 5px;
					}
				}
			}
		}
	}
	& > .ui-content {
		overflow-y:	auto;
		color:		#333333;
	}
}




.button {
	cursor:				pointer;
	display:			inline-block;
	vertical-align:		middle;
	text-decoration:	none;
	margin:				10px;
	border-radius:		5px;
	padding:			0 25px;
	background-color:	rgba(66, 165, 245, 1);
	color:				#ffffff;
	font-size:			14px;
	line-height:		45px;
	font-weight:		600;
	box-shadow:			1px 1px 5px rgba(0, 0, 0, 0.2);
	transition:			transform 0.2s ease-out, background-color 0.2s ease-out, color 0.2s ease-out;
	position:			relative;
	
	&:hover {
		background-color:	darken(rgba(66, 165, 245, 1), 10%);
		text-decoration:	none;
	}
	
	&.main {
		background-color:	rgba(77, 182, 172, 1);
		&:hover {
			background-color:	darken(rgba(77, 182, 172, 1), 10%);
			text-decoration:	none;
		}
	}
	
	&.uploadify {
		/* Repro the uploadify style */
		height:			30px;
	    line-height:	30px;
	    overflow:		hidden;
	    position:		relative;
	    text-align:		center;
	}
	
	&.danger {
		background-color:	rgba(229, 115, 115, 1);
		color:				#ffffff;
		&:hover {
			background-color:	darken(rgba(229, 115, 115, 1), 10%);
			text-decoration:	none;
		}
	}
	
	&.admin {
		background-color:	rgba(120, 144, 156, 1);
		color:				#ffffff;
		&:hover {
			background-color:	darken(rgba(120, 144, 156, 1), 10%);
			text-decoration:	none;
		}
	}
	
	&.positive {
		background-color:	rgba(102, 187, 106, 1);
		color:				#ffffff;
		&:hover {
			background-color:	darken(rgba(102, 187, 106, 1), 10%);
			text-decoration:	none;
		}
	}
	
	&.negative {
		background-color:	rgba(251, 140, 0, 1);
		color:				#ffffff;
		&:hover {
			background-color:	darken(rgba(251, 140, 0, 1), 10%);
			text-decoration:	none;
		}
	}
	
	&.disabled {
		background-color:	rgba(0,0,0,0.5);
		color:				#333333;
		text-decoration:	none;
		cursor:				default;
		&:hover {
			background-color:	rgba(0,0,0,0.5);
			color:				#333333;
			text-decoration:	none;
			cursor:				default;
		}
	}
	
	&.sep {
		padding:			0 !important;
		width:				1px;
		height:				20px;
		background-color:	rgba(0,0,0,0.6);
		cursor:				default;
	}
	
	.alert-dot {
		position:			absolute;
		top:				5px;
		right:				10px;
		width:				10px;
		height:				10px;
		background-color:	rgba(255, 0, 0, 1);
		border-radius:		50%;
	}
	
	&.small {
		padding:			0 15px;
		font-size:			12px;
		line-height:		30px
	}
}

