/*
	Role editor UI
*/
.role-editor {
	.type-ahead {
		input {
			width:		100%;
			padding:	2px 4px;
		}
		& > div {
			& > div {
				position:	relative;
				& > div {
					position:	absolute;
					width:		100%;
				}
			}
		}
	}
	& > table {
		width:	100%;
		& > tbody {
			& > tr {
				border-bottom:	1px dotted rgba(0,0,0,0.2);
				& > td {
					padding:		5px;
					font-size:		14px;
					font-weight:	500;
					color:			#333333;
					&.role {
						width:	100px;
						select {
							width:	100%;
						}
					}
					&.del {
						width:		30px;
						text-align:	center;
					}
				}
				&:last-child {
					border-bottom:	0;
				}
			}
		}
	}
}


.ui-profile {
	img {
