}

.form-box {
	border:				1px solid rgba(0,0,0,0.8);
	background-color:	rgba(255,255,255, 1);
	max-width:			800px;
	margin:				20px auto;
	& > .header {
		padding:			10px;
		border-bottom:		1px solid rgba(0,0,0,0.8);
		background-color:	darken(rgba(226, 230, 236, 1), 10%);
		.title {
			font-size:		16px;
			font-weight:	900;
			color:			#333333;
			line-height:	25px;
