			font-size:		12px;
			font-weight:	600;
			color:			#333333;
			line-height:	20px;
		}
	}
}

.ui-form {
	display:	table;
	& > div {
		display:	table-row;
		& > div {
			display:		table-cell;
			padding:		5px;
			vertical-align:	middle;
			input {
				padding:	3px;
				&[type=text] {
					width:		50%;
					min-width:	200px;
				}
				&[type=number] {
					width:		15%;
					min-width:	50px;
				}
				display:	inline-block;
			}
			textarea {
				padding:	5px;
				width:		50%;
				height:		70px;
			}
			.info {
				display:		inline-block;
				padding:		0 10px;
				font-size:		12px;
				font-weight:	500;
				color:			rgba(0,0,0,0.6);
				width:			50%;
				vertical-align:	middle;
			}
			&:first-child {
				font-size:		12px;
				font-weight:	700;
				color:			#333333;
				text-align:		right;
			}
			&:last-child {
				font-size:		12px;
				font-weight:	500;
				color:			#333333;
			}
		}
	}
}

.ctas {
	text-align:		right;
	padding:		10px 30px;
