	}
}

.ui-tabs {
	background-color:	lighten(rgba(31, 34, 41, 1), 17%);
	text-align:			center;
	& > div {
		display:		inline-block;
		color:			rgba(255,255,255, 0.3);
		font-weight:	600;
		font-size:		12px;
		border-bottom:	3px solid transparent;
		padding:		10px 25px;
