.ui-table {
	background-color:	#ffffff;
	max-height:			200px;
	overflow-y:			auto;
	&.no-scroll {
		max-height:		none;
	}
	& > .title {
		font-size:		16px;
		font-weight:	600;
		letter-spacing:	1px;
		color:			#333333;
		padding:		5px;
	}
	& > table {
		width:	100%;
		& > thead {
			& > tr {
				& > th {
					padding:		5px;
					font-size:		13px;
					font-weight:	900;
					text-align:		left;
					&.border-right {
						border-right:	1px solid rgba(0,0,0,0.3);
					}
					&.center {
						text-align:	center;
					}
				}
			}
		}
		& > tbody {
			& > tr {
				& > td {
					cursor:			default;
					padding:		5px;
					font-size:		13px;
					font-weight:	500;
					color:			rgba(0,0,0,0.7);
					text-align:		left;
					
					&.border-right {
						border-right:	1px solid rgba(0,0,0,0.3);
					}
					
					&.name {
						max-width:		150px;
						font-weight:	600;
					}
					&.shortcode {
						max-width:		40px;;
					}
					&.date {
						max-width:		110px;
					}
					&.profile {
						width:			200px;
					}
					&.number {
						width:			100px;
						text-align:		right;
					}
					&.action {
						text-align:		right;
						& > span {
							&.link {
								color:			rgba(66, 165, 245, 0.6);
								font-weight:	600;
								padding:		0 5px;
								transition:		color ease-out 0.3s;
								&:hover {
									color:			rgba(66, 165, 245, 1);
								}
							}
						}
					}
					&.toggle {
						text-align:		right;
					}
					.ago {
						font-size:		10px;
						color:			rgba(0,0,0,0.6);
						line-height:	15px;
						font-weight:	500;
					}
				}
				background-color:	rgba(33, 150, 243, 0);
				transition:			background-color ease-out 0.3s;
				&:hover {
					background-color:	rgba(33, 150, 243, 0.3);
				}
				
				&.selected {
					background-color:	rgba(66, 165, 245, 0.2);
					& > td {
						color:				#ffffff;
					}
				}
				&.margin_call {
					background-color:	rgba(0, 0, 0, 0.08);
					& > td {
						color:			rgba(0,0,0,0.3);
					}
				}
				&.profit {
					background-color:	rgba(76, 175, 80, 0.1);
					& > td {
						color:			rgba(0,0,0,0.6);
					}
				}
				&.loss {
					background-color:	rgba(183, 28, 28, 0.07);
					& > td {
						color:			rgba(0,0,0,0.6);
					}
				}
			}
		}
	}
	&.autosize {
		& > table {
			width:	auto;
			margin:	0 auto;
		}
	}
}


/*
	Auth
*/

.ui-auth {
	padding:			10px;
	text-align:			center;
	& > div {
		background-color:	rgba(255,255,255, 1);
		margin:				0 auto;
		display:			inline-block;
		border:				1px solid rgba(0,0,0,0.1);
		border-radius:		5px;
		& > div {
			padding:		5px;
			input {
				min-width:	200px;
				border:		0;
				border-bottom:	1px solid rgba(0,0,0,0.05);
				padding:	5px;
			}
		}
	}
}

.ui-toggle {
	display:			inline-block;
	vertical-align:		middle;
	width:				40px;
	height:				20px;
	border-radius:		10px;
	background-color:	rgba(144, 164, 174, 1);
	position:			relative;
	box-shadow:			inset -1px -1px 5px rgba(0,0,0,0.2);
	border:				1px solid 1px solid rgba(0,0,0,0.1);
	& > div {
		position:			absolute;
		top:				3px;
		left:				3px;
		width:				14px;
		height:				14px;
		border-radius:		50%;
		background-color:	rgba(55, 71, 79, 1);
		transition:			left ease-out 0.3s;
		box-shadow:			1px 1px 5px rgba(0,0,0,0.4);
		border:				1px solid 1px solid rgba(0,0,0,0.1);
	}
	&.on {
		background-color:	rgba(156, 204, 101, 0.5);
		& > div {
			background-color:	rgba(156, 204, 101, 1);
			left:				22px;
		}
	}
	&.off {
		background-color:	rgba(229, 115, 115, 0.5);
		& > div {
			background-color:	rgba(229, 115, 115, 1);
			left:				3px;
		}
	}
}


