﻿.ui-input {
	.ui-input-value {
		color:			rgba(66, 165, 245, 1);
		font-size:		12px;
		font-weight:	500;
	}
	.ui-input-text, .ui-input-url {
		input {
			padding:		5px;
			font-size:		11px;
			font-weight:	500;
			width:			100%;
		}
	}
	.ui-input-list {
		select {
			padding:		5px;
			font-size:		11px;
			font-weight:	500;
			width:			100%;
		}
	}
	.ui-input-drop-dialog {
		position:	relative;
		.drop-dialog {
			position:	absolute;
			bottom:		0;
			left:		50%;
			/*transition: opacity 0.3s @transitionType, transform @transitionDuration @transitionType;
			transform: scale(0.5,0);*/
			opacity:	1;
			z-index:	50000;
			box-shadow:	1px 1px 5px rgba(0,0,0,0.1);
			text-shadow:none;
			& > div, & > span {
				position:	relative;
				& > .arrow {
					position:			absolute;
					top:				-5px;
					left:				-6px;
					z-index:			50000;
					transform:			translateY(8px) rotate(45deg);
					width:				10px;
					height:				10px;
					background-color:	rgba(225, 229, 235, 1);
					box-shadow:			1px 1px 5px rgba(0, 0, 0, 0.6);
				}
				& > .container {
					position:			absolute;
					top:				8px;
					left:				-13px;
					z-index:			50000;
					transform:			translateX(-50%);
					padding:			5px;
					border-radius:		3px;
					background-color:	rgba(225, 229, 235, 1);
					box-shadow:			0px 3px 5px rgba(0, 0, 0, 0.5);
					color:				#333;
					text-align:			center;
					
					.ui-tabs {
						display:		inline-block;
						margin:			0 auto;
						border-radius:	20px;
						& > div {
							border-bottom:		0;
							transition:			color ease-out 0.3s, background-color ease-out 0.3s;
							background-color:	rgba(66, 165, 245, 0);
							&.selected {
								color:				rgba(255, 255, 255, 1);
								background-color:	rgba(66, 165, 245, 1);
								border-bottom:		0;
							}
							
							&:first-child {
								border-radius:	20px 0 0 20px;
							}
							&:last-child {
								border-radius:	0 20px 20px 0;
							}
						}
					}
				}
			}
		}
	}
}

